.emoji {
    font-size: 50px;
    margin: 4px;
    border-radius: 50%;
    border: 12px solid transparent;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: transform .3s cubic-bezier(.4,0,.2,1),border .3s cubic-bezier(.4,0,.2,1);
}

.emoji:focus {
    transform: scale(1);
}

.moodContainer {
    display: inline-flex;
}

.score {
    font-size: 25px;
}

.moodSelected {
    background: #FFE3E6;
}