.App {
  text-align: center;
  margin-top: 18px;
}

.primaryButton {
  cursor: pointer;
  padding: 8px 23px;
  border-radius: 100px;
  height: 40px;
  background: #c6007f;
  color: white;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  border: none;
  margin-left: 8px;
}

.recordInput {
  width: 400px;
  height: 40px;
  padding: 0 17px;
  border: solid 1px #d2d2d2;
  border-radius: 25px;
  -webkit-appearance: none;
  box-sizing: border-box;
  background-color: #fff;
  outline: none;
}

.recordInput:focus {
  border: solid 1px #ffade2;
}

.container:nth-child(n+1) {
  margin-top: 24px;
}


